body {
  margin: 0;
  background-color: #000;
}

.App {
  margin: 0;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App-active {
  background-color: #333;
}

.App-inactive {
  background-color: #000;
  transition: background-color 1s;
}

.App-knuckles {
  display: flex;
  alignitems: center;
  justifycontent: center;
}
